import { reactive, ref, computed } from "vue"
import { useStore } from "vuex"

export default function () {
  // carousel
  const rules = reactive({
    name: [
      { required: true, message: "!", trigger: "blur" },
      { max: 128, message: "64!", trigger: "blur" }
    ],
    start_at: [{ required: true, message: "!", trigger: "blur" }],
    end_at: [{ required: true, message: "!", trigger: "blur" }],
    quota_per_month: [
      { required: true, message: "!", trigger: "blur" },
      { type: "number", message: "!", trigger: "blur" }
    ]
  })

  const formRef = ref(null)

  const store = useStore()
  const list = computed(() => store.getters.clinic.clinics)
  const fullList = computed(() => store.getters.clinic.fullClinics)
  const listLoading = computed(() => store.getters.clinic.listLoading)

  const form = reactive({
    clinic_id: 0,
    name: "",
    start_at: "",
    end_at: "",
    quota_per_month: 1
  })

  const clearForm = {
    clinic_id: 0,
    name: "",
    start_at: "",
    end_at: "",
    quota_per_month: 0
  }

  return {
    store,
    list,
    fullList,
    listLoading,
    rules,
    formRef,
    form,
    clearForm
  }
}
